.App {
  height: 100vh;
  width: 100vw;
  background-color: rgb(247, 247, 244);
}

body {
  margin: 0;
  padding: 0;
}

.Sidebar {
  height: 100%;
  width: 200px;
  background-color: #e6e8e6;
}

.SidebarList {
  height: 80%;
  width: 100%;
}

.SidebarList.row {
  width: 100%;
  height: 60px;
  list-style-type: none;
  margin: 0%;
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: center;
  align-items: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: medium;
}
.SidebarList#active {
  background-color: #e6e8e6;
}

.row #title {
  flex: 70%;
  display: grid;

  border-bottom-width: 2em;
}

.row #Icon {
  flex: 30%;
  display: grid;
  place-items: center;
}
